.card {
  border: 10px;
  border-color: black;
  position: relative;
  width: 100%;
  /* height: 100%; */
  padding: 20px;
  max-width: 85vw;
  height: 50vh;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  background-color: white;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
}

.card-header {
  display: flex;
  flex-direction: row;
}

.card > h3 {
  color: black;
  width: 100%;
}

.tinderCards__cardContainer {
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}

.swipe {
  width: 100%;
  height: 100%;
  padding-left: 10%;
  position: absolute;
}

.cut-text {
  color: black;
  width: 95%;

  text-overflow: ellipsis;
  overflow: hidden;
  /* width: 160px; */
  /* background-color: red; */
  height: 50%;
  /* height: 4.2em; */
}

.e-label {
  margin: 5px;
}
